import React from 'react';
import { graphql, PageProps } from 'gatsby';
import Layout from '../components/layout';
import SEO from '../components/seo';

type Props = PageProps<GatsbyTypes.ProductPageBySlugQuery>;

const ProductTemplate: React.FC<Props> = ({ data }) => {
  const { markdownRemark: product } = data;

  return (
    <Layout>
      <SEO title={product?.frontmatter?.title || ''} />

      <article className="py-16">
        <div className="px-4 sm:px-6 lg:px-8">
          <header className="text-lg max-w-prose mx-auto mb-6">
            {product?.frontmatter?.title && (
              <h1 className="mt-2 mb-8 text-3xl text-center leading-8 font-extrabold tracking-tight text-gray-900 sm:text-4xl sm:leading-10">
                {product.frontmatter.title}
              </h1>
            )}
            {product?.frontmatter?.description && (
              <p className="text-xl text-gray-500 leading-8">{product.frontmatter.description}</p>
            )}
          </header>
          {/* eslint-disable react/no-danger */}
          <section
            className="prose prose-lg text-gray-500 mx-auto"
            dangerouslySetInnerHTML={{ __html: product?.html || '' }}
          />
          {/* eslint-enable react/no-danger */}
        </div>
      </article>
    </Layout>
  );
};

export default ProductTemplate;

export const pageQuery = graphql`
  query ProductPageBySlug($slug: String!) {
    markdownRemark(fields: { slug: { eq: $slug } }) {
      html
      frontmatter {
        title
        description
      }
    }
  }
`;
